.mobile-nav-bg {
  display: flex;
  position: absolute;
  left: -100%;
  z-index: 2;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  transition: 0.3s ease all;
  transform: translateX(0);
  top: 0;
  &.active {
    transform: translateX(100%);
  }
}

.mobile-nav {
  width: 50%;
  z-index: 3;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  // background: #000;
  background: url("../../../assests/navigationbar/sidenav.jpg");
  background-position: right;
  background-size: cover;
  background-repeat: no-repeat;
  a {
    color: #ce9c52;
    padding: 5px 10px;
    margin-bottom: 0.5rem;
    display: flex;
    align-items: center;
    white-space: nowrap;
    svg {
      margin-right: 5px;
      min-width: 1rem;
      min-height: 1rem;
    }
  }
}
.mobile-mask {
  width: 50%;
  height: 100%;
  z-index: 3;
}
hr{
  background: #222;
  width: 100%;
}
@keyframes sidenav {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}
