} */
@media screen and (max-width: 768px) {
  .menu {
    display: flex;
  }
  .page-title {
    text-align: center;
  }
  .page-container {
    padding: 1rem 0;
  }

}
@media screen and (max-width: 575px) {
  video{
    right: -213%;
    bottom: 0%;
  }
  .container, .container-fluid{
    padding: 0 !important;
  }
}
