.landing-page{height:100vh;width:100%;display:flex;align-items:center;justify-content:center;flex-direction:column;background:#000}.landing-video{right:0;position:relative}.landing-content{position:fixed;bottom:10%}.language-wrapper{display:flex;justify-content:space-around;align-items:center}.landing-language-select{display:flex;flex-direction:column;justify-content:center;align-items:center;width:10rem;height:5rem;cursor:pointer}.landing-language-select img{width:4rem;height:4rem;margin-bottom:1rem}.landing-language-select span{color:#fff;white-space:nowrap;font-size:.9rem}@media screen and (max-width: 1920){.landing-video{right:-65px}}@media screen and (max-width: 1024px){.landing-video{right:-76%;position:fixed}.landing-content{bottom:5%}}@media screen and (max-width: 575px){.landing-video{width:250%;height:100%}.landing-language-select{width:7rem;height:100%}.landing-language-select img{width:3rem;height:3rem}.landing-language-select span{font-size:.7rem}}

